html {
  font-size: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 1rem;
}
@media (min-width: 40em) {
  body {
    height: 100vh;
    display: grid;
    place-content: center;
  }
}

.top-left-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 55%;
  border-radius: 0 0 62.5rem 0;
  background: linear-gradient(to top, var(--lt-violet), var(--vry-lt-magenta));
}
@media (min-width: 40em) {
  .top-left-bg {
    width: 40%;
    height: 95%;
    left: -8.9375rem;
    border-radius: 0 0 62.5rem 62.5rem;
  }
}

.bot-right-bg {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50%;
  height: 55%;
  border-radius: 62.5rem 0 0 0;
  background-color: var(--lt-gr-violet);
  z-index: -1;
}
@media (min-width: 40em) {
  .bot-right-bg {
    right: -15rem;
    width: 40%;
    height: 90%;
    border-radius: 62.5rem 62.5rem 0 0;
  }
}

.grid {
  display: grid;
  gap: 4.375rem;
}
@media (min-width: 40em) {
  .grid {
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}